import {
  Button,
  Chip,
  Spinner,
  Switch,
} from "@heroui/react";
import { useCallback, useState } from "react";
import { usePathname } from "next/navigation";

const statusColorMap = {
  true: "success",
  1: "success",
  false: "danger",
  2: "danger",
};

export const TableRenderCell = ({
  data,
  columnKey,
  setMethod,
  setIsOpen,
  onRowSelected,
}) => {

  const pathname = usePathname();

  const [loading, setloading] = useState(false);
  const renderCell = useCallback((data, columnKey) => {
    const cellValue = data[columnKey];
    switch (columnKey) {
      case "status":
        return (
          <div>
            {loading ? (
              <Spinner color="primary" size="sm" />
            ) : (
              <Switch
                className="capitalize"
                color={statusColorMap[cellValue]}
                isSelected={cellValue === 1}
                onChange={() => {
                  setMethod("status");
                  setIsOpen(false);
                  onRowSelected(data);
                  setloading(true);
                }}
                size="sm"
                variant="flat"
              >
                <Chip
                  className="capitalize"
                  color={statusColorMap[cellValue]}
                  size="sm"
                  variant="flat"
                >
                  {cellValue === true
                    ? "Yes"
                    : cellValue === 1
                      ? "Active"
                      : cellValue === false
                        ? "No"
                        : "Inactive"}
                </Chip>
              </Switch>
            )}
          </div>
        );
      case "actions":
        return (
          <div className="relative flex justify-center items-center gap-2 group">
            <Button
              isIconOnly
              size="sm"
              variant="light"
              onPress={() => {
                setMethod("Edit"), setIsOpen(true), onRowSelected(data);
              }}
            >
              <FiEdit className="h-4 w-4" />
            </Button>
            <div
              className={` ${(pathname === "/design-your-own" || pathname === "/billing") ? "hidden" : ""}`}
            >
              <Button
                isIconOnly
                size="sm"
                variant="light"
                onPress={() => {
                  setMethod("Delete"), setIsOpen(false), onRowSelected(data);
                }}
              >
                <RiDeleteBinLine className="h-4 w-4" />
              </Button>
            </div>
          </div>
        );
      case "rolesActions":
        return (
          <div className="relative flex justify-start items-center gap-2 group">
            <Button
              isIconOnly
              size="sm"
              variant="light"
              onPress={() => {
                setMethod("Edit"), setIsOpen(true), onRowSelected(data);
              }}
            >
              <FiEdit className="h-4 w-4" />
            </Button>
          </div>
        );
      case "isActive":
        return (
          <div>
            {loading ? (
              <Spinner color="primary" size="sm" />
            ) : (
              <Chip
                className="capitalize"
                color={statusColorMap[cellValue]}
                size="sm"
                variant="flat"
              >
                {cellValue === true ? "Active" : "Inactive"}
              </Chip>
            )}
          </div>
        )
     
      default:
        return cellValue?.length > 200
          ? `${cellValue.slice(0, 200)}...`
          : cellValue;
    }
  }, []);

  return renderCell(data, columnKey);
};
